import React from "react";

import {
  Row,
  Col,
  Button,
} from 'reactstrap';


// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";
import { InlineNumberField, InlineAsyncSelectField, InlineTextField, InlineDateField } from "components/InlineControl";
// import { withRouter } from 'react-router-dom';
import { Label } from 'components/FormControl';
 

export default class UserSupervisorGrid extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
    };

  }
    
  render() {


    const { standardProps, calculateTotalAmount, ...restProps } = this.props;
    const { values, errors,data, isSubmitted } = standardProps;
 
    //let data = values.UserSupervisorList = values.UserSupervisorList || [];

    return (

      <div className="inlineEdit my-3">
  
        <ReactTable
          data={data}
          className="-striped"
          pageSize={data.length || 1}
          showPagination={false}
          sortable={false}

          columns={[
            {
              Header: "#",
              accessor: "col1",
              width: 50,
              resizable: false,
              Cell: cellInfo => {
                return <div className='form-control'>
                  {cellInfo.index + 1}
                </div>
              }
            }, 
            
            {
              Header: <Label>User ID</Label>,
              accessor: "UserID",
              Cell: cellInfo => {
                return <InlineAsyncSelectField
                  name="UserID"
                  label="UserID"
                  tableName="UserProfile" 
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Supervisor Role ID</Label>,
              accessor: "SupervisorRoleID",
              Cell: cellInfo => {
                return <InlineAsyncSelectField
                  name="SupervisorRoleID"
                  label="SupervisorRoleID"
                  tableName="SupervisorRole" 
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Supervisor User ID</Label>,
              accessor: "SupervisorUserID",
              Cell: cellInfo => {
                return <InlineAsyncSelectField
                  name="SupervisorUserID"
                  label="SupervisorUserID"
                  tableName="UserProfile" 
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              width: 40,
              resizable: false,
              accessor: "col2",
              Cell: cellInfo => {
                return (
                  <div disabled={values.IsLocked} className="text-center text-danger lineButton" role="button" onClick={e => {
                    
                    data.splice(cellInfo.index, 1);
                    this.setState({});

                  }}>
                    <span className="fa fa-minus "></span>
                  </div>
                )
              }
            }
          ]}
        />

        <Button disabled={values.IsLocked} color="primary" className="mt-2" onClick={e => {
          data.push({ 
          });
          this.setState({});
        }}><i className="fa fa-plus"></i></Button>

      </div>

    )
  }
} 



