import React from "react";

// import { 
//   Button, 
// } from 'reactstrap';

// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";
import { InlineAsyncSelectField, InlineDateTimeField, InlineTextField, InlineNumberField, InlineDateField } from "../../components/InlineControl";
import { Label } from '../../components/FormControl';

export default class UserLocationList extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      selectedAll: false,
      countSelected: 0
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    const { values } = this.props;

    console.log("values: ", values, "nextProps.values: ", nextProps.values)

    if (values.UserCode != nextProps.values.UserCode) { //count selected
      var countSelected = 0;
      nextProps.data.forEach(locObj => {
        if (locObj.Status == "Y") countSelected++;
      });
      this.state.countSelected = countSelected;
    }

    return true;
  }

  onCheckboxBtnSelectAll() {
    const { data } = this.props;

    data.forEach(locObj => {
      locObj.Status = this.state.countSelected == data.length ? "N" : "Y";
    });

    this.state.countSelected = this.state.countSelected != data.length ? data.length : 0;

    this.setState({});
  }

  onCheckboxBtnClick(selectedLocation) {

    if (selectedLocation.Status == "Y") {
      selectedLocation.Status = "N"
      this.state.countSelected--;
    } else {
      selectedLocation.Status = "Y"
      this.state.countSelected++;
    }

    this.setState({});
  }

  render() {

    const { values, data, ...restProps } = this.props;

    return (
      <div className="inlineEdit my-3">

        <ReactTable
          data={data}
          className="-striped"
          pageSize={data.length > 5 ? 5 : data.length || 1}
          showPagination={data.length > 5}
          pageText={<Label>Page</Label>}
          previousText={<Label>Previous</Label>}
          nextText={<Label>Next</Label>}
          ofText="/"
          sortable={false}
          showPageSizeOptions={false}

          columns={[
            {
              Header: () => {
                return (
                  data.length> 0 ?
                  <div className="text-center" role="button" style={{ cursor: "pointer" }} onClick={() => this.onCheckboxBtnSelectAll()}>
                    <span style={{ fontSize: 17 }}>
                      <i className={data.length > 0 && this.state.countSelected == data.length ? 'fa fa-check-square-o' : 'fa fa-square-o'} />
                    </span>
                  </div>
                  : ''
                );
              },
              accessor: "Seq",
              resizable: false,
              width: 45,
              Cell: cellInfo => {
                return (
                  <div className="text-center" style={{ cursor: "pointer", padding: '7px 0' }}
                    onClick={() => this.onCheckboxBtnClick(data[cellInfo.index])}>
                    <span style={{ fontSize: 17 }}>
                      <i className={data[cellInfo.index].Status == "Y" ? 'fa fa-check-square-o' : 'fa fa-square-o'} />
                    </span>
                  </div>
                )
              },
            },
            {
              Header: <Label>Location</Label>,
              accessor: "LocationDesc",
              Cell: cellInfo => {
                return <div className='form-control'>
                  {cellInfo.value}
                </div>
              }
            }
          ]}
        />

      </div>
    )
  }
}



