import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class ApprovalTaskMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Approval ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="ApprovalID"
                            {...standardProps}
                            createAble={true}
                            tableName="ApprovalTask"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Form ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="FormID" placeholder="Form ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Form No</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="FormNo" placeholder="Form No" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>User ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="UserID" placeholder="User ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Stage</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Stage" placeholder="Stage" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Action</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Action" placeholder="Action" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Reassign Delegate User ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ReassignDelegateUserID" placeholder="Reassign Delegate User ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Reason</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Reason" placeholder="Reason" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Other Reason</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="OtherReason" placeholder="Other Reason" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Comment</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Comment" placeholder="Comment" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Receive Date</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <DateField name="ReceiveDate" placeholder="Receive Date" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Complete Date</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <DateField name="CompleteDate" placeholder="Complete Date" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


