import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class ExpensesCodeMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Expenses Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="ExpenseCode"
                            {...standardProps}
                            createAble={true}
                            tableName="ExpensesCode"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Expenses Code Desc</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ExpenseCodeDesc" placeholder="Expenses Code Desc" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Account Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="AccountCode" placeholder="Account Code" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis1</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis1" placeholder="Analysis1" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis2</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis2" placeholder="Analysis2" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis3</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis3" placeholder="Analysis3" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis4</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis4" placeholder="Analysis4" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis5</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis5" placeholder="Analysis5" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


