import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';

import UserSupervisorGrid from '../../containers/WebGen/UserProfile.UserSupervisor';

export default class UserProfileMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>User ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="UserID"
                            {...standardProps}
                            createAble={true}
                            tableName="UserProfile"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Permission</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Permission" placeholder="Permission" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Role</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Role" placeholder="Role" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>User Name</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="UserName" placeholder="User Name" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Email</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Email" placeholder="Email" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Department</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="Department"
                            {...standardProps} 
                            tableName="DepartmentMaster" 
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis1</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis1" placeholder="Analysis1" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis2</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis2" placeholder="Analysis2" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis3</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis3" placeholder="Analysis3" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis4</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis4" placeholder="Analysis4" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Analysis5</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Analysis5" placeholder="Analysis5" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Is Locked</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="IsLocked" placeholder="Is Locked" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Login Count</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <NumberField name="LoginCount" placeholder="Login Count" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Is Active</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="IsActive" placeholder="Is Active" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            
            <UserSupervisorGrid 
                    data={values.UserSupervisorList = values.UserSupervisorList || []}
                    values={values}
                    errors={errors}
                    isSubmitted={isSubmitted}
                    bodyEvent={this.props.bodyEvent}
                  />

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


