import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, DisplayJson, CheckboxField, Label } from "../../components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  CardTitle,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';
import classnames from 'classnames';

import { ajaxPost, history, loginInfo, downloadFile, setUserPreference } from "../../global";

var getUserPreference = props => {
  const { values } = props.standardProps;
  if (loginInfo.UserPreference && loginInfo.UserPreference != "") {
    loginInfo.UserPreference.Language = loginInfo.language;
    Object.assign(values, loginInfo.UserPreference);
  }
}

export default class UserPreference extends React.Component {
  constructor(props) {
    super(props);

    this.state = {

    };

    getUserPreference(props);

    // const { values } = props.standardProps;

    // values.Language = loginInfo.language;
  }

  shouldComponentUpdate(nextProps) {
    if (Object.keys(nextProps.standardProps.values).length == 0)
      Object.assign(nextProps.standardProps.values, loginInfo.UserPreference);
    return true;
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    // if (Object.keys(values).length == 0)
    //   Object.assign(values, loginInfo.UserPreference);

    return (
      <Card className="border-0">
        <CardHeader>
          <Label>User Preference</Label>
        </CardHeader>
        <CardBody style={{ padding: '10px' }}>
          <form autoComplete="off">
            {/* <h6>
              <u><Label>Sidebar</Label></u>
            </h6>
            <Row>
              <Col xs="12">
                <Row className="form-group">
                  <Col xs="12" md="5" className="col-form-label">
                    <Label>Auto Hide</Label>
                  </Col>
                  <Col xs="12" md="7" className="text-right">
                    <CheckboxField
                      name="AutoHide"
                      {...standardProps}
                      tableName="GeneralMaster.Checkbox"
                    />
                  </Col>
                </Row>
              </Col>
            </Row> */}
            <h6>
              <u><Label>Dashboard</Label></u>
            </h6>
            <Row>
              <Col xs="12">
                <Row className="form-group">
                  <Col xs="12" className="col-form-label">
                    <Label>Refresh Frequency</Label>
                  </Col>
                  <Col xs="12">
                    <AsyncSelectField
                      name="RefreshFrequency"
                      {...standardProps}
                      tableName="GeneralMaster.RefreshFrequency"
                      clearable={false}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>

            <Row>
              <Col xs="12">
                <Row className="form-group">
                  <Col xs="12" className="col-form-label">
                    <Label>Language</Label>
                  </Col>
                  <Col xs="12">
                    <AsyncSelectField
                      name="Language"
                      {...standardProps}
                      tableName="GeneralMaster.SystemLanguage"
                      clearable={false}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>

            <div className="text-right mt-2">
              <Button color="success" onClick={() => { setUserPreference(values); }} ><i className="fa fa-refresh"></i> <Label>Refresh</Label></Button>
            </div>
          </form>
          {/* <DisplayJson {...values} /> */}

        </CardBody>
      </Card>

    )

  }
}



