import React from "react";

import {
  Row,
  Col,
  Button,
} from 'reactstrap';


// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";
import { InlineNumberField, InlineAsyncSelectField, InlineTextField, InlineDateField } from "components/InlineControl";
// import { withRouter } from 'react-router-dom';
import { Label } from 'components/FormControl';
 

export default class WorkflowStageGrid extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
    };

  }
    
  render() {


    const { standardProps, calculateTotalAmount, ...restProps } = this.props;
    const { values, errors,data, isSubmitted } = standardProps;
 
    //let data = values.WorkflowStageList = values.WorkflowStageList || [];

    return (

      <div className="inlineEdit my-3">
  
        <ReactTable
          data={data}
          className="-striped"
          pageSize={data.length || 1}
          showPagination={false}
          sortable={false}

          columns={[
            {
              Header: "#",
              accessor: "col1",
              width: 50,
              resizable: false,
              Cell: cellInfo => {
                return <div className='form-control'>
                  {cellInfo.index + 1}
                </div>
              }
            }, 
            
            {
              Header: <Label>Workflow ID</Label>,
              accessor: "WorkflowID",
              Cell: cellInfo => {
                return <InlineTextField
                  name="WorkflowID"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Version</Label>,
              accessor: "Version",
              Cell: cellInfo => {
                return <InlineTextField
                  name="Version"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Stage ID</Label>,
              accessor: "StageID",
              Cell: cellInfo => {
                return <InlineNumberField
                  name="StageID"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>SeqNo</Label>,
              accessor: "SeqNo",
              Cell: cellInfo => {
                return <InlineNumberField
                  name="SeqNo"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Stage Name</Label>,
              accessor: "StageName",
              Cell: cellInfo => {
                return <InlineTextField
                  name="StageName"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Recipient</Label>,
              accessor: "Recipient",
              Cell: cellInfo => {
                return <InlineTextField
                  name="Recipient"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Is Auto Approval</Label>,
              accessor: "IsAutoApproval",
              Cell: cellInfo => {
                return <InlineTextField
                  name="IsAutoApproval"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              Header: <Label>Is Can Edit</Label>,
              accessor: "IsCanEdit",
              Cell: cellInfo => {
                return <InlineTextField
                  name="IsCanEdit"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },


            {
              width: 40,
              resizable: false,
              accessor: "col2",
              Cell: cellInfo => {
                return (
                  <div disabled={values.IsLocked} className="text-center text-danger lineButton" role="button" onClick={e => {
                    
                    data.splice(cellInfo.index, 1);
                    this.setState({});

                  }}>
                    <span className="fa fa-minus "></span>
                  </div>
                )
              }
            }
          ]}
        />

        <Button disabled={values.IsLocked} color="primary" className="mt-2" onClick={e => {
          data.push({ 
          });
          this.setState({});
        }}><i className="fa fa-plus"></i></Button>

      </div>

    )
  }
} 



