import GeneralCalendar from '../../views/GeneralCalendar/GeneralCalendar'
import WithFormEvent from '../../components/WithFormEvent'
import { ajaxPost } from '../../global';

var getGeneralCalendar = function (values, bodyEvent) {
  var url = 'FB.GetGeneralCalendar';
  var data = { CalendarType: values.CalendarType, Year: values.TargetYear };
  ajaxPost(url, data).then(response => {
    console.log("GetGeneralCalendar", response);

    if (!response || response.length == 0) {
      response = [];
    }
    if (bodyEvent)
      bodyEvent.updateCalendarList(response);
  });
}


export default WithFormEvent(GeneralCalendar, {

  onSubmit: function (values, callback) {

    if (!values.CalendarType || !values.TargetYear) {
      alert("Please Select Supplier Code and Target Year");
      return;
    }

    var url = 'FB.SaveGeneralCalendar';
    var data = { 
      Supplier: values.CalendarType,
      Year: values.TargetYear,
      Obj: JSON.stringify(values.GeneralCalendarList)
     };
    ajaxPost(url, data).then(response => {
      console.log("SaveGeneralCalendar", response);
      if (response.message)
        alert(response.message)

      if (callback)
        callback({
          values: values
        });
    });

  },
  
  fieldChange: {
    CalendarType: function (values, updateFieldComponent, setFieldValue, me) {
      if (values.CalendarType && values.TargetYear) {
        getGeneralCalendar(values, me.bodyEvent)
      }
      if (!values.CalendarType) {
        setFieldValue("TargetYear", undefined);
        updateFieldComponent("TargetYear", true);
        me.bodyEvent.updateCalendarList([]);
      }
    },
    TargetYear: function (values, updateFieldComponent, setFieldValue, me) {
      if (values.CalendarType && values.TargetYear) {
        getGeneralCalendar(values, me.bodyEvent)
      }
      if (!values.TargetYear) {
        me.bodyEvent.updateCalendarList([]);
      }
    },
  },


})



