import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, DisplayJson, Label } from "../../components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText
} from 'reactstrap';


// Import React Table
import SupplierCalendarBody from '../../containers/SupplierCalendar/SupplierCalendar.Body';

export default class SupplierCalendar extends React.Component {
  constructor(props) {
    super(props)

    const { values } = props.standardProps;
    values.SupplierCalendarList = [];

  }

  componentDidMount() {
    this.props.bodyEvent["updateCalendarList"] = (CalendarList) => {
      console.log("componentDidMount", CalendarList)
      this.updateCalendarList(CalendarList);
    }
  }

  updateCalendarList(CalendarList) {
    console.log("updateCalendarList", CalendarList)

    const { values } = this.props.standardProps;
    values.SupplierCalendarList.length = 0;
    CalendarList.forEach((date) => {
      values.SupplierCalendarList.push(date)
    })
    this.setState({});
  }

  render() {
    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    console.log("render values", values)
    return (

      <div>
        <Row>
          <Col xs="12" sm="12" md="12" lg="12">
            <Card>
              {/* <CardHeader>
                <Label>Supplier Calendar</Label>
              </CardHeader> */}
              <CardBody>
                <form autoComplete="off">

                  <Row>
                    <Col xs="12" md="6">

                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>Supplier Code</Label>
                        </Col>
                        <Col xs="12" md="8">
                          <AsyncSelectField
                            name="SupplierCode"
                            {...standardProps}
                            tableName="MaSupplier"
                          />
                        </Col>
                      </Row>

                    </Col>
                  </Row>


                  <Row>
                    <Col xs="12" md="6">

                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>Year</Label>
                        </Col>
                        <Col xs="12" md="8">
                          <TextField
                            name="TargetYear"
                            {...standardProps}
                          />
                        </Col>
                      </Row>

                    </Col>
                  </Row>

                  <SupplierCalendarBody
                    data={values.SupplierCalendarList || []}
                    values={values}
                    errors={errors}
                    isSubmitted={isSubmitted}
                    bodyEvent={this.props.bodyEvent}
                  />

                  <div className="text-right">
                    <Button color="success" onClick={onSubmit} type="button" ><i className="fa fa-save"></i>  <Label>Submit</Label></Button>
                  </div>

                </form>

              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>

    )
  }
}



