import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, DisplayJson, Label } from "../../components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
} from 'reactstrap';
import classnames from 'classnames';

import UserLocationList from './UserLocation.UserLocationList';
import UserTypeList from './UserLocation.UserTypeList';
import { history } from "../../global";

export default class ClientMaster extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      kiosk: history.location.pathname.startsWith("/Kiosk/Home")
    }
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (
      <div className={this.state.kiosk ? "PRApplicationSearch" : ""}>
        <Row>
          <Col xs="12" sm="12" md="12" lg="12">
            <Card className={this.state.kiosk ? "fullScreenForm" : ""}>
              <CardBody className={this.state.kiosk ? "kiosk-card-body" : ""}>
                <form autoComplete="off">
                  <Row>
                    <Col xs="12" md="12" lg="12" xl="6">
                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>New Password</Label>
                        </Col>
                        <Col xs="12" md="8">

                          <TextField name="password" placeholder="New Password" type="password" {...standardProps} />
                        </Col>
                      </Row>
                    </Col>
                    <Col xs="12" md="12" lg="12" xl="6">
                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>Confirmed Password</Label>
                        </Col>
                        <Col xs="12" md="8">

                          <TextField name="ConfirmedPassword" placeholder="Confirmed Password" type="password" {...standardProps} />
                        </Col>
                      </Row>
                    </Col>
                  </Row>

                  <br />

                  {this.state.kiosk ? <Row>
                    <Col xs={{ size: 6, offset: 6 }} md={{ size: 4, offset: 8 }} xl={{ size: 2, offset: 10 }}>
                      <Button className="kioskButton btn-kiosk-main btn btn-secondary" color="success" onClick={onSubmit} type="button" >
                        <i className="fa fa-save"></i>  <Label>Save</Label></Button>
                    </Col>
                  </Row> :
                    <div className="text-right">
                      <Button color="success" onClick={onSubmit} type="button" ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
                    </div>}

                  <DisplayJson {...values}></DisplayJson>
                </form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>

    )
  }
}


