import React, { Component, Fragment } from 'react';
import {
  Nav,
  NavItem,
  NavbarToggler,
  NavbarBrand,
} from 'reactstrap';
import { loginInfo } from '../../global';

import { Label } from '../FormControl';

import LocationSelector from '../../views/Master/LocationSelector';
import withDraggablePopup from '../withDraggablePopup';
import { history } from "../../global";

const LocationSelectorCtrl = withDraggablePopup(LocationSelector, {
  title: "LocationSelector",
  slideIn: "Down"
})

// function whichTransitionEvent() {
//   var t;
//   var el = document.createElement('fakeelement');
//   var transitions = {
//     'transition': 'transitionend',
//     'OTransition': 'oTransitionEnd',
//     'MozTransition': 'transitionend',
//     'WebkitTransition': 'webkitTransitionEnd'
//   }

//   for (t in transitions) {
//     if (el.style[t] !== undefined) {
//       return transitions[t];
//     }
//   }
// }

class Header extends Component {

  // componentDidMount() {

  //   var e = document.getElementsByClassName('main')[0];


  //   var transitionEvent = whichTransitionEvent();
  //   transitionEvent && e.addEventListener(transitionEvent, function () {
  //     console.log('Transition complete!  This is the callback, no library needed!');
  //     window.dispatchEvent(new Event('resize'));
  //   });
  // }


  shouldComponentUpdate() {
    return true;
  }

  sidebarToggle(e) {
    e.preventDefault();
    document.body.classList.toggle('sidebar-hidden');

    //call window resize after sidebar hide/show animation completed (0.25s)
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
    }, 250);
  }

  sidebarMinimize(e) {
    e.preventDefault();
    document.body.classList.toggle('sidebar-minimized');
  }

  mobileSidebarToggle(e) {
    e.preventDefault();
    document.body.classList.toggle('sidebar-mobile-show');
    document.body.classList.remove('sidebar-hidden');
  }

  asideToggle(e) {
    e.preventDefault();
    document.body.classList.toggle('aside-menu-hidden');

    //call window resize after sidebar hide/show animation completed (0.25s)
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
    }, 250);
  }

  openLocationSelector = () => {
    if (loginInfo.UserLocationList.length > 0)
      this.nameInput.openWindow(loginInfo);
  }

  updateUI = () => {
    console.log(history);
    //this.setState({});
    var currentPath = window.location.hash.replace('#', '');
    history.replace({ pathname: "/empty" });
    setTimeout(() => {
      history.replace({ pathname: currentPath });
    });
  }

  render() {
    return (
      <header className="app-header navbar">
        <LocationSelectorCtrl
          isKiosk={true}
          ref={(input) => {
            this.nameInput = input;
          }}
          updateUI={this.updateUI}
        >
        </LocationSelectorCtrl>
        <NavbarToggler className="d-lg-none" onClick={this.mobileSidebarToggle}>
          {/* <span className="navbar-toggler-icon"></span> */}
          <i className="fa fa-bars"></i>
        </NavbarToggler>
        <NavbarToggler className="d-md-down-none" onClick={this.sidebarToggle}>
          <i className="fa fa-bars"></i>
        </NavbarToggler>
        <div className="mr-auto logo"></div>



        {/*         
        <div className="mr-2"  ><i className="header-user-icon"></i></div>
        <div className="mr-3" style={{ 'lineHeight': '29px' }}><span>{loginInfo.UserID}</span></div>
 */}


        


        {!window.isiPhone && (
          <Fragment>
            <div className="ml-auto mr-2" style={{ cursor: 'pointer' }} onClick={this.openLocationSelector} ><i className="fa fa-location-arrow fa-2x"></i></div>
            <div className="mr-3" onClick={this.openLocationSelector} style={{ fontSize: '18px', lineHeight: '18px', cursor: 'pointer' }}><span>{loginInfo.LocationDesc}</span></div>


            <div style={{ marginLeft: '10px', marginRight: '20px', fontSize: '35px' }}>
              |
            </div>
            <div className="mr-2"  ><i className="header-user-icon"></i></div>
            <div className="mr-4" style={{ fontSize: '18px', lineHeight: '18px' }}><span>{loginInfo.UserName}</span></div>
          </Fragment>
        )}

        <NavbarToggler className="d-md-down-none" onClick={this.asideToggle}>
          <span className="fa fa-bars"></span>
        </NavbarToggler>
      </header>
    );
  }
}

export default Header;



