import React from "react";
import {
  Button,
} from 'reactstrap';

// Import React Table
import ReactTable from "react-table";
import { InlineAsyncSelectField, InlineDateTimeField, InlineTextField, InlineNumberField, InlineDateField } from "../../components/InlineControl";
import { Label } from '../../components/FormControl';

export default class SupplierCalendarBody extends React.Component {

  render() {

    const { standardProps, ...restProps } = this.props;
    const { values, data, errors, isSubmitted } = standardProps;

    //console.log(standardProps);

    return (

      <div className="inlineEdit my-3">

        <ReactTable
          data={data}
          className="-striped"
          pageSize={data.length > 10 ? 10 : data.length || 1}
          showPagination={data.length > 10}
          showPageSizeOptions={false}
          pageText={<Label>Page</Label>}
          previousText={<Label>Previous</Label>}
          nextText={<Label>Next</Label>}
          ofText="/"

          columns={[
            {
              Header: "#",
              accessor: "Seq",
              resizable: false,
              width: 50,
              Cell: cellInfo => {
                return <div className='form-control'>
                  {cellInfo.index + 1}
                </div>
              }
            },
            {
              Header: <Label>Rest Date</Label>,
              accessor: "RestDate",
              Cell: cellInfo => {
                return <InlineDateField
                  name="RestDate"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },
            {
              Header: <Label>English Description</Label>,
              accessor: "EngDesc",
              Cell: cellInfo => {
                return <InlineTextField
                  name="EngDesc"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },
            {
              Header: <Label>Chinese Description</Label>,
              accessor: "ChiDesc",
              Cell: cellInfo => {
                return <InlineTextField
                  name="ChiDesc"
                  index={cellInfo.index}
                  {...standardProps}
                />
              }
            },
            // {
            //   Header: <Label>Convert Rate</Label>,
            //   accessor: "ConvertRate",
            //   Cell: cellInfo => {
            //     return <InlineNumberField
            //       name="ConvertRate"
            //       index={cellInfo.index}
            //       {...standardProps}
            //     />
            //   }
            // },
            {
              width: 40,
              resizable: false,
              accessor: "col2",
              Cell: cellInfo => {
                return (
                  <div className="text-center text-danger lineButton" role="button" onClick={e => {
                    data.splice(cellInfo.index, 1);
                    this.setState({});

                  }}>
                    <span className="fa fa-minus "></span>
                  </div>
                )
              }
            }
          ]}
        />
        {(values.SupplierCode && values.TargetYear) &&
          <Button color="primary" className="mt-2" onClick={e => {
            console.log("push", values)
            data.push({
              SupplierCode: values.SupplierCode
            });
            this.setState({});
          }}><i className="fa fa-plus"></i></Button>
        }
      </div>

    )
  }
}



