import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import { loginInfo } from '../global';

var CheckAccessibility = pathname => {
  return true;
  var path = pathname.startsWith("/Kiosk/Home") ? "/Kiosk/Home" : pathname.startsWith("/Kiosk/ProductLineHome") ? "/Kiosk/ProductLineHome" : pathname;
  return (path == '/Home' || path == '/Home/Procurement' || path == '/Home/Workshop' || (loginInfo.AccessibleUrlList && loginInfo.AccessibleUrlList.hasOwnProperty(path)));
}

const AuthenRoute = ({ component: Component, openTour, setTourCurrentStep, ...rest }) =>
  <Route
    {...rest}
    render={props => {
      // return ((loginInfo.UserID && loginInfo.UserID.coun )
      //   ? <Component {...props} />
      //   : <Redirect
      //     to='/login'
      //   />)

      // //console.log(paths);(loginInfo);
      // //console.log(props);

      if (loginInfo.UserID) {
        if (Component 
          && CheckAccessibility(props.location.pathname)
          )
        return (<Component openTour={openTour} setTourCurrentStep={setTourCurrentStep} {...props } name={rest.name} />);
        else
          return (<Redirect to='/' />);
      }

      else {
        return (<Redirect to='/login' />);
      }


    }}

  />;

export default AuthenRoute;


