import UserShortcut from '../../views/UserShortcut/UserShortcut';
import WithFormEvent from '../../components/WithFormEvent'
import { ajaxPost } from '../../global';

export default WithFormEvent(UserShortcut, {
  
  updateUserShortcut: function (values, callback) {

    var url = 'FB.SaveUserShortcut';
    var data = { Obj: JSON.stringify(values) };
    ajaxPost(url, data).then(response => {
      console.log("Response")

      if (callback)
        callback({
          values: values
        });
    });

  },
})



