import React from "react";
import {
  Button,
} from 'reactstrap';

// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css"; 
import { Label } from "components/FormControl";

export default class RoleFunctionList extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      selectedAll: false,
      countSelected: 0
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    const { values } = this.props.standardProps;

    if (values.RoleID != nextProps.standardProps.values.RoleID) { //count selected
      var countSelected = 0;
      nextProps.standardProps.values.RoleFunctionList.forEach(func => {
        if (func.Status == "Y") countSelected++;
      });
      this.state.countSelected = countSelected;
    }

    return true;
  }

  onCheckboxBtnSelectAll() {
    const { values, data } = this.props.standardProps;

    data.forEach(func => {
      if (this.state.countSelected == data.length)
        func.Status = "N"
      else {
        func.Status = "Y"
      }
    });

    if (this.state.countSelected != data.length)
      this.state.countSelected = data.length;
    else
      this.state.countSelected = 0;

    this.setState({});
  }

  onCheckboxBtnClick(selectedFunction, index) {
    const { values, data } = this.props.standardProps;

    if (selectedFunction.Status == "N") {
      selectedFunction.Status = "Y"
      this.state.countSelected++;
    } else {
      selectedFunction.Status = "N"
      this.state.countSelected--;
    }

    this.setState({});
  }

  render() {

    const { tableNo, standardProps, ...restProps } = this.props;
    const { values, data, errors, isSubmitted } = standardProps;

    return (

      <div className="inlineEdit my-3">

        <ReactTable
          data={data}
          className="-striped"
          pageSize={data.length > 10 ? 10 : data.length || 1}
          showPagination={data.length > 10}
          pageText={<Label>Page</Label>}
          previousText={<Label>Previous</Label>}
          nextText={<Label>Next</Label>}
          ofText="/"
          sortable={false}

          columns={[
            // {
            //   Header: "#",
            //   accessor: "Seq",
            //   resizable: false,
            //   width: 40,
            //   Cell: cellInfo => {
            //     return <div className='form-control'>
            //       {cellInfo.index + 1}
            //     </div>
            //   }
            // },

            {
              Header: cellInfo => {
                return (
                  data.length > 0 ?
                    <div className="text-center" style={{ cursor: "pointer" }} role="button" onClick={() => this.onCheckboxBtnSelectAll()}>
                      <span style={{ fontSize: 17 }}>
                        <i className={data.length > 0 && this.state.countSelected == data.length ? 'fa fa-check-square-o ' : 'fa fa-square-o '} />
                      </span>
                    </div>
                    : ''
                );
              },
              accessor: "Seq",
              resizable: false,
              width: 45,
              Cell: cellInfo => {
                return (
                  <div className="form-control text-center" style={{ cursor: "pointer" }} role="button"
                    onClick={() => this.onCheckboxBtnClick(data[cellInfo.index], cellInfo.index)}>
                    <span style={{ fontSize: 17 }}>
                      {data[cellInfo.index].Status == "Y" && <i className='fa fa-check-square-o' />}
                      {data[cellInfo.index].Status == "N" && <i className='fa fa-square-o' />}
                      {/* <i className={this.state.selected[cellInfo.index] ? 'fa fa-check-square-o ' : 'fa fa-square-o '} /> */}
                    </span>
                  </div>
                  // <div className="text-center">
                  //   <Button
                  //     color="white" style={{ backgroundColor: "transparent" }}
                  //     onClick={() => this.onCheckboxBtnClick(data[cellInfo.index], cellInfo.index)} >
                  //     {data[cellInfo.index].Status == "Y" && <i className='fa fa-check-square-o' />}
                  //     {data[cellInfo.index].Status == "N" && <i className='fa fa-square-o' />}
                  //     {/* <i className={data[cellInfo.index].Status == "Y" ? 'fa fa-check-square-o ' : 'fa fa-square-o '} /> */}
                  //   </Button>
                  // </div>
                )
              },
            },

            {
              Header: <Label>Access Right</Label>,
              accessor: "Description",
              Cell: cellInfo => {
                return <div className='form-control'>
                  {LabelMapping[cellInfo.value] || cellInfo.value}
                </div>
                // <InlineTextField
                //   name="Description"
                //   index={cellInfo.index}
                //   {...standardProps} />
              }
            },
            // {
            //   width: 40,
            //   resizable: false,
            //   accessor: "col2",
            //   Cell: cellInfo => {
            //     return (
            //       <div disabled={values.IsLocked} className="text-center text-danger lineButton" role="button" onClick={e => {
            //         if (values.IsLocked) return;

            //         data.splice(cellInfo.index, 1);
            //         this.setState({});

            //       }}>
            //         <span className="fa fa-minus "></span>
            //       </div>
            //     )
            //   }
            // }
          ]}
        />
        {/* 
        <Button disabled={values.IsLocked} color="primary" className="mt-2" onClick={e => {
          data.push({
            Category: values.Category,
            CategoryDesc: values.CategoryDesc,
          });
          this.setState({});
        }}><i className="fa fa-plus"></i></Button> */}

      </div>

    )
  }
}



