import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class ExpClaimHdrMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Exp Claim ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="ExpClaimID"
                            {...standardProps}
                            createAble={true}
                            tableName="ExpClaimHdr"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Payable To</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="PayableTo" placeholder="Payable To" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Currency</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Currency" placeholder="Currency" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Total Amount</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <NumberField name="TotalAmount" placeholder="Total Amount" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Contact No</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ContactNo" placeholder="Contact No" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Description</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Description" placeholder="Description" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Cheque Available Date</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <DateField name="ChequeAvailableDate" placeholder="Cheque Available Date" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Cheque Name</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ChequeName" placeholder="Cheque Name" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Cheque Address</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ChequeAddress" placeholder="Cheque Address" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Cheque Attention</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ChequeAttention" placeholder="Cheque Attention" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Bank Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="BankCode" placeholder="Bank Code" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Other Bank Name</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="OtherBankName" placeholder="Other Bank Name" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Bank Account No</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="BankAccountNo" placeholder="Bank Account No" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Application Staff ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ApplicationStaffID" placeholder="Application Staff ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Apply Date</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <DateField name="ApplyDate" placeholder="Apply Date" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Stage</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Stage" placeholder="Stage" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Flow ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="FlowID" placeholder="Flow ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Form Validated</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="FormValidated" placeholder="Form Validated" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


