import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';

import WorkflowStageGrid from '../../containers/WebGen/Workflow.WorkflowStage';

export default class WorkflowMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Workflow ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="WorkflowID"
                            {...standardProps}
                            createAble={true}
                            tableName="Workflow"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Active Version</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="Version"
                            {...standardProps}
                            createAble={true}
                            tableName="Workflow"
                            filterKeys={['WorkflowID']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Description</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Description" placeholder="Description" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            
            <WorkflowStageGrid 
                    data={values.WorkflowStageList = values.WorkflowStageList || []}
                    values={values}
                    errors={errors}
                    isSubmitted={isSubmitted}
                    bodyEvent={this.props.bodyEvent}
                  />

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


