import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class GeneralMasterMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Category</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="Category"
                            {...standardProps}
                            createAble={true}
                            tableName="GeneralMaster"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="Code"
                            {...standardProps}
                            createAble={true}
                            tableName="GeneralMaster"
                            filterKeys={['Category']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>English Description</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="EnglishDescription" placeholder="English Description" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Chinese Description</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ChineseDescription" placeholder="Chinese Description" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Is Locked</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="IsLocked" placeholder="Is Locked" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


