 
import ApprDelegation from '../../views/WebGen/ApprDelegation.js' 
import WithFormEvent from 'components/WithFormEvent'
import { ajaxPost, loginInfo, history } from 'global';

const ApprDelegationCtrl = WithFormEvent(ApprDelegation, {

  onSubmit: function (values, callback) {
 
    var url = 'ApprovalModule.SaveApprDelegation'; 
 
    var data = { obj: JSON.stringify(values) };

    ajaxPost(url, data).then(response => {

      if (response && response.message) {
        alert(response.message);
      } 

    });

  },

   getFormData: function (params, callback) {

    var url = 'ApprovalModule.GetApprDelegation';
    var data = { ...params }; 
    ajaxPost(url, data).then(response => {
      console.log("ApprovalModule response: ", response)
      if (callback)
        callback({
          values: response || data
        });
    });

  },
 
})

export default ApprDelegationCtrl;


