import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, DisplayJson, Label } from "../../components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  ButtonGroup,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  ListGroup,
  ListGroupItem
} from 'reactstrap';

import { setLocation, history } from "../../global";

export default class LocationSelector extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selected: this.props.values.LocationCode
    };
  }
  
  updateCurrentLocation = () => {
    const { values, closeWindow } = this.props;
    var index = document.querySelector('input[name=location]:checked').id;
    console.log("checked: ", document.querySelector('input[name=location]:checked').value);
    console.log("values.UserLocationList[index]: ", values.UserLocationList[index]);
    setLocation({
      LocationCode: values.UserLocationList[index].LocationCode,
      LocationDesc: values.UserLocationList[index].LocationDesc,
    });
    if(this.props.updateUI)
      this.props.updateUI();
    closeWindow();
  }

  render() {

    const { values, ...restPops } = this.props;
    return (

      <div className="location-selector" ref={(input) => { this.nameInput = input; }} >

        <div>
          <Row>
            {values.UserLocationList &&

              values.UserLocationList.map((locObj, index) => {
                return (
                  <Col xs="12" key={index}>
                    <label className="radio">
                      <input type="radio" name="location" id={index}
                        checked={this.state.selected === locObj.LocationCode} 
                        onChange={(e) => 
                          this.setState({ selected: e.target.value })}
                        value={locObj.LocationCode}/> 
                      <span>{locObj.LocationDesc}</span>
                    </label>
                  </Col>
                );
              })
            }
          </Row>
        </div>

        <div className="text-right">
          <Button color="primary" className="confirm-btn" 
            onClick={this.updateCurrentLocation}
          ><Label>Confirm</Label>
           </Button>
          <Button color="danger" className="cancel-btn ml-1"
            onClick={() => {
              this.props.closeWindow();
              if (values.BackToMenuIfCancel)
                history.push("/menu");
            }}
          ><Label>Cancel</Label>
           </Button>
        </div>
      </div>

    )
  }
}



