import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class ApprDelegationMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Delegation ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="DelegationID"
                            {...standardProps}
                            createAble={true}
                            tableName="ApprDelegation"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>User ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="UserID" placeholder="User ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Form</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Form" placeholder="Form" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Delegation to User</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="DelegationtoUser" placeholder="Delegation to User" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Date From</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <DateField name="DateFrom" placeholder="Date From" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Date To</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <DateField name="DateTo" placeholder="Date To" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Status</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Status" placeholder="Status" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


