import React, { Component } from 'react';
import { Link, Switch, Route, Redirect } from 'react-router-dom';
import { Container } from 'reactstrap';
import Header from 'components/Header/Header.v2';
import Sidebar from '../../components/Sidebar';
import Breadcrumb from '../../components/Breadcrumb';
import Aside from 'components/Aside/';
import Footer from 'components/Footer/';
import AuthenRoute from 'components/AuthenRoute';
import { routes } from '../../routes';
import LoginCtrl from 'containers/Master/Login';
import UserShortcut from 'containers/UserShortcut/UserShortcut';


import {
  Button, ButtonGroup
} from 'reactstrap';
import { loginInfo, history } from 'global';

