import UserRole from '../../views/SYstem/UserRole'
import WithFormEvent from 'components/WithFormEvent'
import { ajaxPost } from 'global';

export default WithFormEvent(UserRole, {

  onSubmit: function (values, callback) {

    var url = 'SystemModule.SaveUserRole';
    var data = { Obj: JSON.stringify(values) };
    ajaxPost(url, data).then(response => {
      console.log("Response")

      alert('Done');
      if (callback)
        callback({
          values: values
        });
    });

  },

  getFormData: function (params, callback) {
    var url = 'SystemModule.GetUserRole';
    var data = { Code: params.Code };
    ajaxPost(url, data).then(response => {
      console.log("GetUserRole response: ", response)

      if (!response.RoleID) {
        response = {
          RoleID: params.Code,
          RoleUserList: [],
          RoleFunctionList: [],
        };
      }

      if (callback)
        callback({
          values: response
        });
    });
  },

  defaultValues: {
    RoleUserList: [],
    // RoleFunctionList: [],
    RoleFunctionList: []
  },

  validatePattern:{
    RoleID:{
        required:"",
        // pattern: /^\d[2]$/
    },
},


})


