import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class WorkflowRoutingMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Workflow ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="WorkflowID"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowRouting"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Version</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="Version"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowRouting"
                            filterKeys={['WorkflowID']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Stage ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="StageID"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowRouting"
                            filterKeys={['WorkflowID','Version']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Workflow Routing ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="WorkflowRoutingID"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowRouting"
                            filterKeys={['WorkflowID','Version','StageID']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Status</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Status" placeholder="Status" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Next Stage ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="NextStageID" placeholder="Next Stage ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Criteria And Or</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="CriteriaAndOr" placeholder="Criteria And Or" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


