import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, FileField, DisplayJson, Label } from "../../components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText
} from 'reactstrap';
import { ajaxPost } from '../../global';


// Import React Table
import GeneralCalendarBody from '../../containers/GeneralCalendar/GeneralCalendar.Body';

export default class GeneralCalendar extends React.Component {
  constructor(props) {
    super(props)

    this.state = {};
    const { values } = props.standardProps;
    values.GeneralCalendarList = [];

  }

  componentDidMount(){ 
    this.props.bodyEvent["updateCalendarList"] = (CalendarList) =>{
      console.log("componentDidMount", CalendarList)
      this.updateCalendarList(CalendarList);
    }
  }
  
  updateCalendarList(CalendarList) {
    console.log("updateCalendarList", CalendarList)
    
    const { values } = this.props.standardProps;
    values.GeneralCalendarList.length = 0;
    CalendarList.forEach((date) =>{
      values.GeneralCalendarList.push(date)
    })
    this.setState({});
  }

  
  import() {

    var url = 'FB.ImportICSFile';
    var ICSFile = this.props.standardProps.values["ICSFile"];

    var data = {
      CalendarType: this.props.standardProps.values.CalendarType,
      ICSFile: ICSFile,
    };
 
    ajaxPost(url, data).then(response => {
      console.log("ImportICSFile Reponse", response); 

      if (response.message) {
        alert(response.message); 
      }
 
    });
  }


  render() {
    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    console.log("render values", values)
    return (

      <div>
        <Row>
          <Col xs="12" sm="12" md="12" lg="12">
            <Card>
              <CardHeader>
                <Label>General Calendar</Label>
              </CardHeader>
              <CardBody>
                <form autoComplete="off">

                  <Row>
                    <Col xs="12" md="6">

                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>Calendar Type</Label>
                        </Col>
                        <Col xs="12" md="8">
                          <AsyncSelectField
                            name="CalendarType"
                            {...standardProps}
                            tableName="CalendarType"
                          />
                        </Col>
                      </Row>

                    </Col>
                  </Row>


                  <Row>
                    <Col xs="12" md="6">

                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>Year</Label>
                        </Col>
                        <Col xs="12" md="8">
                          <TextField
                            name="TargetYear"
                            {...standardProps}
                          />
                        </Col>
                      </Row>

                    </Col>
                    <Col xs="12" md="6">

                      <Row className="form-group"> 
                        <Col xs="12" md="12">


                          <div style={{ 'display': this.state.Importing ? 'flex' : 'none' }}>

                            <FileField
                              name="ICSFile"
                              {...standardProps}>
                            </FileField>

                            <Button color="success" onClick={e => {

                              this.import();

                            }}><i className="fa fa-upload"></i> <Label>Upload</Label></Button>

                            <Button color="danger" onClick={e => {

                              this.setState({
                                Importing: false
                              });

                            }}><i className="fa fa-close"></i> <Label>Cancel</Label></Button>

                          </div>
                          

        <div style={{ 'display': this.state.Importing ? 'none' : '' }}>
          <Button color="primary" onClick={e => {

            this.setState({
              Importing: !this.state.Importing
            });

          }}><i className="fa fa-upload"></i> <Label>Import</Label></Button>
        </div>
                        </Col>
                      </Row>

                    </Col>
                  </Row>

                  <GeneralCalendarBody
                    data={values.GeneralCalendarList}
                    values={values}
                    errors={errors}
                    isSubmitted={isSubmitted}
                    bodyEvent={this.props.bodyEvent}
                  />

                  <div className="text-right">
                    <Button color="success" onClick={onSubmit} type="button" ><i className="fa fa-save"></i>  <Label>Submit</Label></Button>
                  </div>

                </form>

              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>

    )
  }
}



