import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, DisplayJson, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
} from 'reactstrap'; 

import RoleFunctionList from '../../containers/System/UserRole.FunctionList';
import RoleUserList from '../../containers/System/UserRole.UserList';

export default class ClientMaster extends React.Component {

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (
      <div>
        <Row>
          <Col xs="12" sm="12" md="12" lg="12">
            <Card>
              <CardBody>
                <form autoComplete="off">
                  <Row>
                    <Col xs="12" md="6">
                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>User Role</Label>
                        </Col>
                        <Col xs="12" md="8">

                          <AsyncSelectField
                            name="RoleID"
                            {...standardProps}
                            createAble={true}
                            tableName="UserRole"
                            module="SystemModule"
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="12" md="6">
                      <Row className="form-group">
                        <Col xs="12" md="4" className="col-form-label">
                          <Label>Description</Label>
                        </Col>
                        <Col xs="12" md="8">

                          <TextField
                            name="Description"
                            {...standardProps}

                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>

                  <RoleUserList
                    {...standardProps}
                    data={values.RoleUserList || []}
                    values={values}
                    errors={errors}
                    isSubmitted={isSubmitted}
                    bodyEvent={this.props.bodyEvent}
                  />

                  <RoleFunctionList
                    {...standardProps}
                    data={values.RoleFunctionList || []}
                    values={values}
                    errors={errors}
                    isSubmitted={isSubmitted}
                    bodyEvent={this.props.bodyEvent}
                  />



                  <br />
                  <div className="text-right">
                    <Button color="success" onClick={onSubmit} type="button" ><i className="fa fa-save"></i> <Label>Submit</Label> </Button>
                  </div>

                  <DisplayJson {...values}></DisplayJson>
                </form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>

    )
  }
}


