import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class WorkflowEmailMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Workflow ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="WorkflowID"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowEmail"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Version</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="Version"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowEmail"
                            filterKeys={['WorkflowID']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Stage ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="StageID"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowEmail"
                            filterKeys={['WorkflowID','Version']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Workflow Email ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="WorkflowEmailID"
                            {...standardProps}
                            createAble={true}
                            tableName="WorkflowEmail"
                            filterKeys={['WorkflowID','Version','StageID']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>To User List</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ToUserList" placeholder="To User List" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>CC User List</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="CCUserList" placeholder="CC User List" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>BCC User List</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="BCCUserList" placeholder="BCC User List" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Content</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Content" placeholder="Content" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Email Before After</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="EmailBeforeAfter" placeholder="Email Before After" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


