// import Login from '../../views/Pages/Login'
import Login from '../../views/Pages/Login/Login.v2'
// import Login from '../../views/Pages/Login/Login.Kiosk'
import WithFormEvent from '../../components/WithFormEvent'
import { ajaxPost, loginInfo, history } from '../../global';

const LoginCtrl = WithFormEvent(Login, {

  onSubmit: function (values, callback) {

    //var url = 'HttpHandler/LoginHandler.ashx';
    var url = 'Login';
    //var data = { action: 'login', ...values };

    values.UserID = values.UserID ? values.UserID : "MASTER";
    var data = { UserCode: values.UserID, Password: values.Password || "" };

    ajaxPost(url, data).then(response => {

      if (response.Session) {

        Object.assign(loginInfo, response);
        loginInfo.UserID = values.UserID.toUpperCase();

        //get location code
        url = 'FB.GetUserConfig';
        data = { Code: loginInfo.UserID, language: loginInfo.language };
        ajaxPost(url, data).then(response => {

          console.log(response);
          console.log(loginInfo);
          if (response) {
            Object.assign(loginInfo, response);
          }

          loginInfo.save();
          loginInfo.startUpdateSession();

          console.log(loginInfo);
          if (loginInfo.UserType == "" && loginInfo.UserTypeList.length > 1)
            history.push('/menu');
          // else if(loginInfo.Role == "Management"){
          //   history.push('/HomeManagement');
          // }
          else {
            if (loginInfo.UserType == "S" || loginInfo.UserTypeList[0] == "S")
              history.push('/Kiosk/Home');
            else if (loginInfo.UserType == "L" || loginInfo.UserTypeList[0] == "L")
              history.push('/Kiosk/ProductLineHome');
            else if (loginInfo.UserType == "M" || loginInfo.UserTypeList[0] == "M")
              history.push('/HomeManagement')
            else
              history.push('/');
          }

        });

      } else {
        alert('failure to login');
      }

    });

  },


})

export default LoginCtrl;


