import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, DisplayJson, CheckboxField, Label } from "../../components/FormControl";
import {
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  CardTitle,
  Collapse,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';

import { loginInfo, updateUserShortcutStatus } from "../../global";

var debounce = require('debounce-promise');

export default class UserPreference extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      filterShortcut: '',
      lastUpdateTime: new Date(),

    }
    const { values } = this.props.standardProps;
    values.UserCode = loginInfo.UserCode;

    //store current obj;
    window.userShortcutObj = this;
  }

  autoFocus = () => {
    this.inputName.focus();
  }

  searchItem = debounce(searchTerm => {
    this.setState({ filterShortcut: searchTerm });
  }, 500);


  clearItem = () => {
    this.setState({ filterShortcut: "" });
  };

  filterItem = (shortcut) => {
    if (this.state.filterShortcut) {
      if (LabelMapping[shortcut.Description]) {
        return (LabelMapping[shortcut.Description].toUpperCase().includes(this.state.filterShortcut.toUpperCase())
          || shortcut.Description.toUpperCase().includes(this.state.filterShortcut.toUpperCase()))
      }
      else
        return shortcut.Description.toUpperCase().includes(this.state.filterShortcut.toUpperCase());
    }
    else
      return shortcut.Status == "Y";
  }

  render() {

    const { standardProps, isAfterSave, updateUserShortcut, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;
    if (!values.UserShortcutList)
      values.UserShortcutList = loginInfo.UserShortcutList

    return (
      <div className="shortcut-sidebar">
        <div className="shortcut-background" onClick={() => {

          document.body.classList.toggle('shortcut-sidebar-hidden');
        }}>

        </div>
        <Card className="shortcut-container">
          <CardHeader>
            <div className="input-group">
              <div className="input-group-prepend" style={{ padding: '15px 5px 12px 12px' }}>
                <span className="fa fa-search" ></span>
              </div>
              <input type="text" className="form-control border-0" placeholder={LabelMapping["Function Name"] || "Function Name"} ref={(ref) => {
                this.inputName = ref;
              }}
                onChange={e => {
                  this.searchItem(e.target.value);
                }} />
              <div className="input-group-append" style={{ padding: '15px 15px 12px 12px', cursor: 'pointer' }} onClick={e => {
                this.inputName.value = "";
                this.clearItem();
              }}>
                <span className="fa fa-close"></span>
              </div>
            </div>
          </CardHeader>
          <CardBody>
            <form autoComplete="off">
              <div className="item-container">
                {values.UserShortcutList && values.UserShortcutList.map((shortcut, index) => {
                  if (this.filterItem(shortcut))
                    return (
                      <div key={index} className="shortcut-item">
                        <a href={"#/" + shortcut.Url}><Label>{shortcut.Description}</Label></a>
                        <i className={`fa fa-lg mt-1 ${shortcut.Status == "Y" ? 'fa-minus-square-o' : 'fa-plus-square-o active'}`} onClick={() => {
                          shortcut.Status = shortcut.Status == "Y" ? "N" : "Y";
                          updateUserShortcut(shortcut, () => {
                            updateUserShortcutStatus(shortcut.Status, index);
                            this.setState({ lastUpdateTime: new Date() });
                          });
                        }}></i>
                      </div>
                    )
                })}
              </div>
            </form>

          </CardBody>
        </Card>
      </div>
    )

  }
}



