import SupplierCalendar from '../../views/SupplierCalendar/SupplierCalendar'
import WithFormEvent from '../../components/WithFormEvent'
import { ajaxPost } from '../../global';

var getSupplierCalendar = function (values, bodyEvent) {
  var url = 'FB.GetSupplierCalendar';
  var data = { SupplierCode: values.SupplierCode, Year: values.TargetYear };
  ajaxPost(url, data).then(response => {
    console.log("GetSupplierCalendar", response);

    if (!response || response.length == 0) {
      response = [];
    }
    if (bodyEvent)
      bodyEvent.updateCalendarList(response);
  });
}


export default WithFormEvent(SupplierCalendar, {

  onSubmit: function (values, callback) {

    if (!values.SupplierCode || !values.TargetYear) {
      alert("Please Select Supplier Code and Target Year");
      return;
    }

    var url = 'FB.SaveSupplierCalendar';
    var data = { 
      Supplier: values.SupplierCode,
      Year: values.TargetYear,
      Obj: JSON.stringify(values.SupplierCalendarList)
     };
    ajaxPost(url, data).then(response => {
      console.log("SaveSupplierCalendar", response);
      if (response.message)
        alert(response.message)

      if (callback)
        callback({
          values: values
        });
    });

  },
  // getFormData: function (params, callback) {
  //   var url = 'FB.GetMaUnitHd';
  //   var data = { Code: params.Code };
  //   ajaxPost(url, data).then(response => {
  //     console.log("GetMaUnitHd", response);

  //     if (!response || !response.UnitCode) {
  //       response = {
  //         ...params,
  //         MaUnitDetList: []
  //       }
  //     }

  //     if (callback)
  //       callback({
  //         values: response
  //       });
  //   });
  // },

  fieldChange: {
    SupplierCode: function (values, updateFieldComponent, setFieldValue, me) {
      if (values.SupplierCode && values.TargetYear) {
        getSupplierCalendar(values, me.bodyEvent)
      }
      if (!values.SupplierCode) {
        setFieldValue("TargetYear", undefined);
        updateFieldComponent("TargetYear", true);
        me.bodyEvent.updateCalendarList([]);
      }
    },
    TargetYear: function (values, updateFieldComponent, setFieldValue, me) {
      if (values.SupplierCode && values.TargetYear) {
        getSupplierCalendar(values, me.bodyEvent)
      }
      if (!values.TargetYear) {
        me.bodyEvent.updateCalendarList([]);
      }
    },
  },


})



