import React from "react";
import {
  Button,
} from 'reactstrap';

// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";
import { InlineAsyncSelectField, InlineDateTimeField, InlineTextField, InlineNumberField, InlineDateField } from "components/InlineControl";
import { Label } from "components/FormControl";
export default class ProductMasterDimension extends React.Component {

  render() {

    const { tableNo, standardProps, ...restProps } = this.props;
    const { values, data, errors, isSubmitted } = standardProps;

    return (

      <div className="inlineEdit my-3">

        <ReactTable
          data={data}
          className="-striped"
          pageSize={data.length || 1}
          showPagination={false}
          sortable={false}

          columns={[
            {
              Header: "#",
              accessor: "Seq",
              resizable: false,
              width: 50,
              Cell: cellInfo => {
                return <div className='form-control'>
                  {cellInfo.index + 1}
                </div>
              }
            },
            {
              Header: <Label>User</Label>,
              accessor: "User",
              Cell: cellInfo => {
                return <InlineAsyncSelectField
                  name="UserCode"
                  label="UserCode"
                  index={cellInfo.index}
                  tableName="UserProfile"
                  {...standardProps} />
              }
            },
            {
              width: 40,
              resizable: false,
              accessor: "col2",
              Cell: cellInfo => {
                return (
                  <div disabled={values.IsLocked} className="text-center text-danger lineButton" role="button" onClick={e => {
                    if (values.IsLocked) return;

                    data.splice(cellInfo.index, 1);
                    this.setState({});

                  }}>
                    <span className="fa fa-minus "></span>
                  </div>
                )
              }
            }
          ]}
        />

        <Button color="primary" className="mt-2" onClick={e => {
          data.push({
            Category: values.Category,
            CategoryDesc: values.CategoryDesc,
          });
          this.setState({});
        }}><i className="fa fa-plus"></i></Button>

      </div>

    )
  }
}



