import React from "react";
import { AsyncSelectField, DateTimeField, DateField, TextField, NumberField, Label } from "components/FormControl";
import {
  Row,
  Col,
  Button,
  Card,
  CardBody,
} from 'reactstrap';



export default class ExpClaimItemMaster extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const { standardProps, isAfterSave, onSubmit, getFormData, ...restProps } = this.props;
    const { values, errors, isSubmitted } = standardProps;

    return (

      <Card>
        <CardBody>
          <form autoComplete="off">
            <Row>
              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>ExpClaimID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="ExpClaimID"
                            {...standardProps}
                            createAble={true}
                            tableName="ExpClaimItem"
                            filterKeys={[]}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Item ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <AsyncSelectField
                            name="ItemID"
                            {...standardProps}
                            createAble={true}
                            tableName="ExpClaimItem"
                            filterKeys={['ExpClaimID']}
                            getFormData={getFormData}
                            isAfterSave={isAfterSave}
                          />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Expense Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ExpenseCode" placeholder="Expense Code" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Amount</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <NumberField name="Amount" placeholder="Amount" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Cost Centre Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="CostCentreCode" placeholder="Cost Centre Code" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Project Code</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ProjectCode" placeholder="Project Code" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Contract No</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ContractNo" placeholder="Contract No" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Project Manager ID</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ProjectManagerID" placeholder="Project Manager ID" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Project Manager Seen</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="ProjectManagerSeen" placeholder="Project Manager Seen" {...standardProps} />
                  </Col>
                </Row>
              </Col>

              <Col xs="12" md="6" >
                <Row className="form-group">
                  <Col xs="12" md="4" className="col-form-label">
                    <Label>Remarks</Label>
                  </Col>
                  <Col xs="12" md="8">
                    <TextField name="Remarks" placeholder="Remarks" {...standardProps} />
                  </Col>
                </Row>
              </Col>


            </Row>

            
            

            <div className="text-right">
              <Button color="success" onClick={onSubmit} ><i className="fa fa-save"></i>  <Label>Save</Label></Button>
            </div>
          </form>
        </CardBody>
      </Card>


    )
  }
}


