import React from 'react';
import {Route, Link} from 'react-router-dom';
import {Breadcrumb, BreadcrumbItem} from 'reactstrap';
import {routes} from '../../routes';
// import { loginInfo } from '../../global';
import {DynamicLabel} from 'components/FormControl'

const routeDict = {};
routes.map((value, index) => {
  routeDict[value.path] = value.name;
});
 

const findRouteName = url => {
  // if(url === "/Home" && loginInfo.RoleUserList){
  //   if (loginInfo.RoleUserList.includes("Procurement"))
  //     return "Procurement Dashboard";
  //   else if (loginInfo.RoleUserList.includes("ProductLine"))
  //     return "Product Line Dashboard";
  //   else if (loginInfo.RoleUserList.includes("Workshop"))
  //     return "Workshop Dashboard";
  // }
  
  return LabelMappingDict[routeDict[url]] || routeDict[url];
};

const getPaths = (pathname) => {
  const paths = ['/'];

  if (pathname === '/') return paths;

  pathname.split('/').reduce((prev, curr, index) => {
    const currPath = `${prev}/${curr}`;
    paths.push(currPath);
    return currPath;
  });
  return paths;
};

const BreadcrumbsItem = ({...rest, match}) => {
  const routeName = findRouteName(match.url);
  if (routeName) {
    return (
      match.isExact ?
        (
          // match.url === "/Home" ?
          //   <BreadcrumbItem active><div className='m-0' style={{fontSize:'18px', fontWeight:'bold'}}><DynamicLabel canUpdate={true}>{routeName}</DynamicLabel></div></BreadcrumbItem>
          //   :
            <BreadcrumbItem active><DynamicLabel canUpdate={true}>{routeName}</DynamicLabel></BreadcrumbItem>
        ) :
        (
          <BreadcrumbItem>
            <Link to={match.url || ''}>
              <DynamicLabel canUpdate={true}>{routeName}</DynamicLabel>
            </Link>
          </BreadcrumbItem>
        )
    );
  }
  return null;
};


//Added by Ning
const urlNoBreadcrumb = ['/Home', '/HomeManagement'];

const Breadcrumbs = ({...rest, location : {pathname}, match}) => {
  const paths = getPaths(pathname);
  const items = paths.map((path, i) => <Route key={i++} path={path} component={BreadcrumbsItem}/>);

  console.log(paths);

  return (
    urlNoBreadcrumb.includes(match.url)  ? ''
    :
    <Breadcrumb>
      {items}
    </Breadcrumb>
  );
};

export default props => (
  <div>
    <Route path="/:path" component={Breadcrumbs} {...props} />
  </div>
);



